* {
  margin: 0;
  padding: 0;
}

#root {
    box-sizing: border-box;
    height: 93vh;
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    outline: none;
    border-radius: 0;
}

body {
    /* background-image: url(https://images.unsplash.com/photo-1494587416117-f102a2ac0a8d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2850&q=80); */
