div.goalItem-indent {
    margin-left: 5%;
    width: 95%;
}

.goalitem-strike {
    -webkit-animation: strike 750ms ease 1 forwards;
            animation: strike 750ms ease 1 forwards;
}

@-webkit-keyframes strike{
    0%   { opacity : 1 }
    100% { 
            -webkit-text-decoration: line-through #ffffff; 
                    text-decoration: line-through #ffffff;
            opacity: 0.75
         }
}

@keyframes strike{
    0%   { opacity : 1 }
    100% { 
            -webkit-text-decoration: line-through #ffffff; 
                    text-decoration: line-through #ffffff;
            opacity: 0.75
         }
}
