div.goalitem-indent {
    margin-left: 5%;
}

.goalitem-strike {
    -webkit-animation: strike 750ms ease 1 forwards;
            animation: strike 750ms ease 1 forwards;
}

div.goalItem-progress {
    margin: 0 24px 0 16px;
    overflow: visible;
    background-color: rgba(229, 199, 234, 0.274);
}

