div.goalItem-progress>div::after {
    background: linear-gradient(to right, #ab47bc 0%, rgb(229, 199, 234) 100%);
    /* border: 1px dotted white; */
    box-shadow: 10px 0 10px rgba(229, 199, 234, 0.5);
    border-radius: 100px;
    content: "";
    width: 120px;
    height: 4px;
    position: absolute;
    right: 0;
    /* white-space: nowrap; */
}

@-webkit-keyframes strike{
    0%   { opacity : 1 }
    100% {
            -webkit-text-decoration: line-through #ffffff;
                    text-decoration: line-through #ffffff;
            opacity: 0.75
         }
}

@keyframes strike{
    0%   { opacity : 1 }
    100% {
            -webkit-text-decoration: line-through #ffffff;
                    text-decoration: line-through #ffffff;
            opacity: 0.75
         }
}
.centeredModal {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    align-items: center; 
}
.settings-wrapper {
    max-width: 960px;
    margin: auto;
    padding-top: 16px;
    /* display: flex;
    flex-direction: column;
    align-items: center; */
}

.settings__column-wrapper, .settings__column-wrapper--center {
    /* max-width: 300px; */
    /* margin: auto; */
    display: flex;
    flex-direction: column;
}

.settings__column-wrapper--center {
    align-items: center;
}

.settings__setting-row {
    margin: 32px 0;
}

h4.settings-title {
    margin: 16px 0;
}
.dash {
    position: relative;
    width: 100%;
    /* height: 100%; */
    max-width: 960px;
    margin: auto;
}

.dash-items {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: center; */
    align-items: center;
    margin: 11px 32px -5px 16px;
    position: -webkit-sticky;
    position: sticky;
    top: 10px;
    z-index: 1;
    /* width: 100%; */
}

.dash-item {
    margin: 3px;
}

.dash-item:first-child {
    margin-left: 0;
}

.dash-item:last-child {
    margin-right: 0;
}

#dash-addBtn {
    position: fixed;
    bottom: 40px;
    right: 40px;
    padding: 0;
}

#dash-addBtn:hover {
    padding: 0 16px;
}

#dash-addBtn:hover::after {
    content:"Add Goal";
    white-space:nowrap;
}
.about {
    height: 100%;
    max-width: 100%;
}

.about-card {
    margin: 16px;
}

.about-card a {
    text-decoration: underline;
}
.App {
    height: 100%;
    width: 100%;
}
